@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-style: normal;
  font-weight: 300;
}

html {
  font-family: Avenir, sans-serif;
}

input[type="search"]::-webkit-search-cancel-button {
  display: none;
}

body {
  margin: 0;
}

:root {
  --text-color: #3d4e61;
}

.search-bars {
  flex-direction: column;
  margin: 0 8px;
  display: flex;
}

.search-bar {
  box-sizing: border-box;
  color: var(--text-color);
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  width: 100%;
  margin-top: 20px;
  font-family: inherit;
  transition: all .3s;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search-bar__big {
  background-position: 25px 24px;
  background-size: 19px 19px;
  height: 70px;
  padding: 0 62px;
  font-size: 16px;
  font-weight: 600;
}

.search-bar__small {
  background-position: 12px 14px;
  background-size: 11px 11px;
  height: 42px;
  padding: 0 33px;
  font-size: 14px;
  font-weight: 600;
  line-height: 19px;
}

.search-bar:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.search-bar::placeholder {
  color: var(--text-color);
  font-weight: 300;
}

.search-bar:focus {
  outline: none;
  box-shadow: 0 4px 4px #00000040;
}

.search-bar__big::placeholder {
  font-size: 16px;
}

.search-bar__small::placeholder {
  font-size: 14px;
}

